          <b-card border-variant="info" header="–ë–∞–ª–∞–Ω—Å –Ω–∞ –∫–æ—Å–º–µ—Ç–∏–∫—É" align="center">
            <b-card-text>
              <h2>{{ cosmeticsBalance }}</h2>
            </b-card-text>
          </b-card>
          <b-card border-variant="info" header="–ë–∞–ª–∞–Ω—Å –Ω–∞ —à–º–æ—Ç–∫–∏" align="center">
            <b-card-text>
              <h2>{{ clothesBalance }}</h2>
            </b-card-text>
          </b-card>
          <b-card border-variant="info" header="–û–±—â–∏–π –±–∞–ª–∞–Ω—Å" align="center">
            <b-card-text>
              <h2>{{ balance }}</h2>
            </b-card-text>
          </b-card>
        </b-card-group>
      </div>
    </section>
    <br>
    <h1>–ü–æ—Å–ª–µ–¥–Ω–∏–µ –ø–æ–∫—É–ø–∫–∏</h1>
    <hr>
    <div>
      <b-card bg-variant="light">
        <b-form-group
          label-cols-lg="3"
          label="–î–æ–±–∞–≤–∏—Ç—å –ø–æ–∫—É–ø–∫—É"
