<script>
import PurchService from "../services/PurchService";

export default {
  name: "Purchases",
  data() {
    return {
      currentDate: new Date(),
      balance: 0,
      cosmeticsBalance: 0,
      clothesBalance: 0,
      loading: true,
      error: "",
      purchases: [],
      typeOptions: [
        { text: "–ö–æ—Å–º–µ—Ç–∏–∫–∞", value: "cosmetics" },
        { text: "–û–¥–µ–∂–¥–∞", value: "clothes" }
      ],
      selectedType: "cosmetics",
      text: "",
      amount: "",
      purchDate: PurchService.convert(new Date())
    };
  },
  computed: {
    currentMonth: function() {
      var months = [
        "–Ø–Ω–≤–∞—Ä—å",
        "–§–µ–≤—Ä–∞–ª—å",
        "–ú–∞—Ä—Ç",
        "–ê–ø—Ä–µ–ª—å",
        "–ú–∞–π",
        "–ò—é–Ω—å",
        "–ò—é–ª—å",
        "–ê–≤–≥—É—Å—Ç",
        "–°–µ–Ω—Ç—è–±—Ä—å",
        "–û–∫—Ç—è–±—Ä—å",
        "–ù–æ—è–±—Ä—å",
        "–Ø–Ω–≤–∞—Ä—å"
      ];
      var thisMonth = months[this.currentDate.getMonth()];

      return thisMonth.toLocaleString("ru-ru", { month: "long" });
    }
  },
  methods: {
    async getBalance() {
      try {
        this.balance = await PurchService.getBalance(this.currentDate);
        this.cosmeticsBalance = await PurchService.getBalanceByType(
          this.currentDate,
          "cosmetics"
        );
        this.clothesBalance = await PurchService.getBalanceByType(
          this.currentDate,
          "clothes"
        );
        this.loading = false;
      } catch (err) {
        this.error = err.message;
      }
    },
    async getPurchases() {
      try {
        this.purchases = await PurchService.getPurchases();
        this.loading = false;
      } catch (err) {
        this.error = err.message;
      }
    },
    clear–°reatePurchVars() {
      this.text = "";
      this.amount = "";
      this.purchDate = PurchService.convert(new Date());
    },
    async createPurch() {
      try {
        await PurchService.createPurchase(
          this.text,
          this.amount,
          this.selectedType,
          this.purchDate
        );
        await this.getPurchases();
        await this.getBalance();
        this.clear–°reatePurchVars();
      } catch (err) {
        this.error = err.message;
      }
    },
    async deletePurch(id) {
      try {
        await PurchService.deletePurchase(id);
        await this.getPurchases();
      } catch (err) {
        this.error = err.message;
      }
    }
  },
  async mounted() {
    await this.getPurchases();
    await this.getBalance();
  },
  async created() {
    await this.getPurchases();
    await this.getBalance();
  }
};
</script>
